window.onload = () => {

  
  let canvas = document.getElementById('canvas');
  let ctx = canvas.getContext('2d');

  canvas.width = window.innerWidth;
  canvas.height = window.innerHeight;

  // ================================== Player =======================================

  class Player {

    constructor (name, srcImg, posX, posY, cols, rows, sizeX, sizeY) {
      this.name = name;
      this.health = 100; // Editar
      this.damage = 20; // Editar
      this.speed = 10; // Editar

    }
  }

  // function drawImage() {
  //   updateFrame();
  //   ctx.drawImage(character, srcX, srcY, width, height, x, y, 150, 150);
  // }



  let character = document.createElement('img');
  character.src = 'mainChar_newMoveSprite_v0.1.svg';

  // The position where the frame will be draw
  let x = 500;
  let y = window.innerHeight - 130 - 20;

  let srcX = 0;
  let srcY = 0;

  //Pro newMoveSrite
  let spriteWidth = 5745;
  let spriteHeight = 610;

  // Frames Count
  let cols = 18;
  let rows = 2;

  let width = spriteWidth / cols; // FrameWidth
  let height = spriteHeight / rows; // FrameHeight

  let frameCount = 16;

  let curFrame = 0;

  let trackLeft = 0;
  let trackRight = 1;

  function updateFrame() {
    ctx.clearRect(x, y,width,height);
    curFrame = ++curFrame % frameCount; // Att animation
    srcX = curFrame * width;
    srcY = 0;
  }
  // ====================================================================================

  

  function drawImage() {
    updateFrame();
    ctx.drawImage(character, srcX, srcY, width, height, x, y, 130, 130);
  }

  setInterval(function() {
    drawImage();
  }, 50);

  function moveLeft(){
    left = true; 
    right = false;
  }

  function moveRight(){
	left = false;
	right = true; 
  }
}

